import { get } from '../api/client';
import { getState, setState, setCurrentNinja } from '../state';
import { navigate } from '../router';
import type { NinjaListResponse, Ninja } from '../types';

function getBeltClass(courseName: string): string {
  const beltName = courseName.toLowerCase();
  if (beltName.includes('white')) return 'belt-white';
  if (beltName.includes('yellow')) return 'belt-yellow';
  if (beltName.includes('orange')) return 'belt-orange';
  if (beltName.includes('green')) return 'belt-green';
  if (beltName.includes('blue')) return 'belt-blue';
  if (beltName.includes('purple')) return 'belt-purple';
  if (beltName.includes('red')) return 'belt-red';
  if (beltName.includes('brown')) return 'belt-brown';
  if (beltName.includes('black')) return 'belt-black';
  return 'belt-black';
}

export async function renderLogin() {
  const container = document.getElementById('app')!;
  const { facilityId } = getState();

  container.innerHTML = `
    <div class="login-container">
      <div class="login-card">
        <header class="login-header">
          <img src="/CodeNinjasLogo.svg" alt="Code Ninjas" class="login-logo" />
          <h1 class="login-title">NinjaBux</h1>
          <p class="login-subtitle">Select your profile to continue</p>
        </header>

        <div class="search-container">
          <input
            type="text"
            id="ninja-search"
            class="search-input"
            placeholder="Search by name..."
          />
        </div>

        <div id="ninja-grid" class="ninja-grid">
          <div class="loading">Loading ninjas...</div>
        </div>
      </div>
    </div>
  `;

  const res = await get<NinjaListResponse>(`/facilities/${facilityId}/ninjas`);
  const gridContainer = document.getElementById('ninja-grid')!;

  if (res.error) {
    gridContainer.innerHTML = `<div class="error">Failed to load ninjas: ${res.error}</div>`;
    return;
  }

  const ninjas = res.data!.ninjas;

  function renderNinjaCards(filteredNinjas: Ninja[]) {
    if (filteredNinjas.length === 0) {
      gridContainer.innerHTML = `<div class="empty">No ninjas found</div>`;
      return;
    }

    gridContainer.innerHTML = filteredNinjas.map(ninja => `
      <div class="ninja-card" data-student-id="${ninja.studentId}">
        <div class="ninja-avatar">${ninja.firstName.charAt(0)}${ninja.lastName.charAt(0)}</div>
        <div class="ninja-name">${ninja.firstName} ${ninja.lastName}</div>
        <div class="ninja-belt belt-badge ${getBeltClass(ninja.courseName)}">${ninja.courseName}</div>
        <div class="ninja-balance">${ninja.currentBalance} Bux</div>
      </div>
    `).join('');

    // Add click handlers
    gridContainer.querySelectorAll('.ninja-card').forEach(card => {
      card.addEventListener('click', () => {
        const studentId = card.getAttribute('data-student-id')!;
        const selectedNinja = filteredNinjas.find(n => n.studentId === studentId)!;
        setState({ studentId });
        setCurrentNinja(selectedNinja);
        navigate('/dashboard');
      });
    });
  }

  renderNinjaCards(ninjas);

  // Search functionality
  const searchInput = document.getElementById('ninja-search') as HTMLInputElement;
  searchInput.addEventListener('input', () => {
    const query = searchInput.value.toLowerCase();
    const filtered = ninjas.filter(ninja =>
      ninja.firstName.toLowerCase().includes(query) ||
      ninja.lastName.toLowerCase().includes(query)
    );
    renderNinjaCards(filtered);
  });
}
