import { get } from '../api/client';
import { getState, setCurrentNinja, isLoggedIn } from '../state';
import { navigate } from '../router';
import { renderNavbar } from '../components/navbar';
import type { Ninja, LedgerResponse, LedgerTxn, TxnType } from '../types';

function getTxnIcon(type: TxnType): string {
  switch (type) {
    case 'INITIAL_BALANCE': return '+';
    case 'ACTIVITY_REWARD': return '+';
    case 'PURCHASE': return '-';
    case 'ADJUSTMENT': return '~';
    default: return '';
  }
}

function getTxnClass(amount: number): string {
  if (amount > 0) return 'positive';
  if (amount < 0) return 'negative';
  return 'neutral';
}

function formatDate(dateStr: string): string {
  const date = new Date(dateStr);
  return date.toLocaleDateString('en-US', {
    month: 'short',
    day: 'numeric',
    hour: 'numeric',
    minute: '2-digit'
  });
}

function renderTransactions(transactions: LedgerTxn[]): string {
  if (transactions.length === 0) {
    return '<li class="empty">No recent transactions</li>';
  }

  return transactions.map(txn => `
    <li class="transaction-item ${getTxnClass(txn.amount)}">
      <span class="transaction-icon">${getTxnIcon(txn.type)}</span>
      <div class="transaction-details">
        <span class="transaction-desc">${txn.description}</span>
        <span class="transaction-date">${formatDate(txn.createdAt)}</span>
      </div>
      <span class="transaction-amount">${txn.amount > 0 ? '+' : ''}${txn.amount}</span>
    </li>
  `).join('');
}

export async function renderDashboard() {
  const container = document.getElementById('app')!;

  if (!isLoggedIn()) {
    navigate('/');
    return;
  }

  const { facilityId, studentId, currentNinja } = getState();

  container.innerHTML = `
    ${renderNavbar()}
    <main class="dashboard-page">
      <div class="loading">Loading dashboard...</div>
    </main>
  `;

  // Fetch ninja details and ledger in parallel
  const ninjaRes = currentNinja
    ? { data: currentNinja, error: undefined }
    : await get<Ninja>(`/facilities/${facilityId}/ninjas/${studentId}`);
  const ledgerRes = await get<LedgerResponse>(`/facilities/${facilityId}/ninjas/${studentId}/ledger?limit=10`);

  const mainContainer = container.querySelector('main')!;

  if (ninjaRes.error || ledgerRes.error) {
    mainContainer.innerHTML = `<div class="error">Failed to load dashboard</div>`;
    return;
  }

  const ninja = ninjaRes.data!;
  setCurrentNinja(ninja);

  const transactions = ledgerRes.data!.transactions;

  mainContainer.innerHTML = `
    <div class="dashboard-container">
      <div class="dashboard">
        <header class="dashboard-header">
          <h1>Welcome, ${ninja.firstName}!</h1>
          <div class="ninja-info">
            <span class="belt-badge belt-${ninja.courseName.toLowerCase().replace(' ', '-')}">${ninja.courseName}</span>
            <span class="level-badge">${ninja.levelName}</span>
          </div>
        </header>

        <div class="balance-card">
          <div class="balance-label">Your Balance</div>
          <div class="balance-amount">${ninja.currentBalance} <span class="balance-unit">Bux</span></div>
        </div>

        <section class="recent-activity">
          <h2>Recent Activity</h2>
          <ul class="transaction-list">
            ${renderTransactions(transactions)}
          </ul>
        </section>

        <div class="quick-links">
          <a href="#/shop" class="btn btn-primary">Shop</a>
          <a href="#/leaderboard" class="btn btn-secondary">Leaderboard</a>
        </div>
      </div>
    </div>
  `;
}
